.root {
  width: 100%;
}

.app {
  max-width: 800px;
  margin: 0 auto;
}

.header {
  width: 100%;
}

.footer {
  width: 100%;
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* ------------------------- player ------------------------- */

/* chip */

.playerChips {
  display: flex;
  align-items: center;
  justify-items: flex-start;
}

.playerChip {
  display: flex;
  align-items: center;
  justify-items: flex-start;
  background: #fff;
  box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.1);
  border: 1px solid #777;
  border-radius: 20px;
  padding-top: 2px;
  padding-left: 2px;
}

.playerChip--badge {
  width: auto;
  margin-top: 15px;
  margin-right: 10px;
}

.playerChip--avatar {

}

.playerChip--score {
  padding: 0 10px;
  margin-top: -6px;
}

/* ------------------------- selectable ------------------------- */

.selected {
  /* border: 2px solid gold; */
  /* border-radius: 10px; */
  border-color: gold;
  cursor: pointer;
  background-color: #fff9d6;
}

.selectable {
  /* border: 2px solid white; */
  /* border-radius: 10px; */
  cursor: pointer;
}

.selectable:hover {
  /* border: 2px solid gold; */
  /* border-radius: 10px; */
  border-color: gold;
  background-color: #fff9d6;
}

.disabled {
  opacity: 0.2;
}

.avatar.selectable {
  border: 2px solid white;
}

.avatar.selected,
.avatar.selectable:hover {
  border: 2px solid gold;
}

/* ------------------------- card ------------------------- */

/* card */

.card {
  background: #fff;
  /* -webkit-box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1); */
  /* box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1); */
  box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.1);
  border: 1px solid #777;
  border-radius: 10px;
  aspect-ratio: 3/5;
  padding: 5px;
}

.card--empty {
  /* margin-top: 3px; */
  /* margin-left: 3px; */
  /* background: rgba(0, 0, 0, 0.1); */
  /* border-color: rgba(0, 0, 0, 0.1); */
  /* background: rgba(0, 0, 0, 0.05); */
  border: 1px dashed #aaa;
  box-shadow: none;
  /* background: repeating-linear-gradient( 45deg, #eee5 0px, #eee 2px, #777 2px, #777 4px ); */
  /* opacity: 0.2; */
}

.card--badge {
  width: auto;
  /* margin-top: 15px; */
  margin-right: 10px;
}

.card--inside {
  width: calc( 100% - 10px );
  height: calc( 100% - 10px );
  border-radius: 10px;
}

/* card value */

.card--value {
  font-weight: bold;
}

/* card default skeleton */

.card {  
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;  
}

.card--inside {  
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;  
}

.card--header {
  height: 20%; 
  align-self: flex-start;

  display: flex;
  justify-content: left;
  align-items: center;
}

.card--content {
  height: 60%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.card--footer {
  height: 20%;  
  align-self: flex-end;

  display: flex;
  justify-content: right;
  align-items: bottom;
}

.card--symbol {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card--symbol svg {
  width: 100%;
}

/* card XS */

.card.size-XS {
  width: 30px;  
}

.card.size-XS .card--value {
  font-size: 16px;
}

.card.size-XS .card--symbol {
  width: 12px;
}

.card.size-XS .card--header,
.card.size-XS .card--footer {
  display: none;
}

/* card S */

.card.size-S {
  width: 40px;
}

.card.size-S .card--value {
  font-size: 23px;
}

.card.size-S .card--symbol {
  width: 17px;
}

.card.size-S .card--header,
.card.size-S .card--footer {
  display: none;
}

/* card M */

.card.size-M {
  width: 50px;
}

.card.size-M .card--header .card--symbol,
.card.size-M .card--footer .card--symbol {
  width: 10px;
}

.card.size-M .card--value {
  font-size: 30px;
}

.card.size-M .card--content .card--symbol {
  width: 20px;
}

/* card L */

.card.size-L {
  width: 60px;
}

.card.size-L .card--header .card--symbol,
.card.size-L .card--footer .card--symbol {
  width: 12px;
}

.card.size-L .card--value {
  font-size: 35px;
}

.card.size-L .card--content .card--symbol {
  width: 22px;
}

/* card XL */

.card.size-XL {
  width: 70px;
}

.card.size-XL .card--header .card--symbol,
.card.size-XL .card--footer .card--symbol {
  width: 15px;
}

.card.size-XL .card--value {
  font-size: 40px;
}

.card.size-XL .card--content .card--symbol {
  width: 27px;
}

/* card color */
 
svg.icon {
  fill: currentColor;
}

.color-black {
  color: #343a40;
}
 
.color-white {
  color: #e9ecef;
}
 
.color-gray {
  color: #adb5bd;
}
 
.color-yellow {
  color: #fee440;
}
 
.color-red {
  color: #ff595e;
}
 
.color-green {
  color: #52b788;
}
 
.color-blue {
  color: #00bbf9;
}
 
.color-purple {
  color: #9b5de5;
}
 
.color-orange {
  color: #ff9f1c;
}
 
.color-pink {
  color: #f15bb5;
}
 
.color-brown {
  color: #9d6b53;
}

/* ------------------------- avatar ------------------------- */
/* cf. https://dribbble.com/shots/16176055-Monster-Avatars-Icons */

.avatar {
  background-image: url(/amnezic/static/media/avatars-dhigital.e3d969dd.webp);
  /* border: 1px solid red; */
  display: inline-block;
  border-radius: 50%;
}

.avatar-XS {
  width: 25px;
  height: 25px;
  background-size: 153px;
  --avatar-start-x: -15px;
  --avatar-start-y: -9px;
  --avatar-delta-x: -24px;
  --avatar-delta-y: -24px;
}

.avatar-S {
  width: 30px;
  height: 30px;
  background-size: 191px;
  --avatar-start-x: -19px;
  --avatar-start-y: -11px;
  --avatar-delta-x: -30px;
  --avatar-delta-y: -30px;
}

.avatar-M {
  width: 40px;
  height: 40px;
  background-size: 254px;
  --avatar-start-x: -26px;
  --avatar-start-y: -15px;
  --avatar-delta-x: -40px;
  --avatar-delta-y: -40px;
}

.avatar-L {
  width: 50px;
  height: 50px;
  background-size: 309px;
  --avatar-start-x: -31px;
  --avatar-start-y: -18px;
  --avatar-delta-x: -49px;
  --avatar-delta-y: -48px;
}

.avatar-XL {
  width: 60px;
  height: 60px;
  background-size: 390px;
  --avatar-start-x: -42px;
  --avatar-start-y: -24px;
  --avatar-delta-x: -61px;
  --avatar-delta-y: -61px;
}

.avatar-1 {
  background-position-x: calc( var(--avatar-start-x) + 0 * var(--avatar-delta-x) );
  background-position-y: calc( var(--avatar-start-y) + 0 * var(--avatar-delta-y) );
}

.avatar-2 {
